import React from 'react';
import {connect} from 'react-redux';

import { scaleLinear } from '@vx/scale';
import { extent, max, min} from 'd3-array';
import { AreaClosed } from '@vx/shape';
import { Group} from '@vx/group';
import { AxisLeft, AxisBottom } from '@vx/axis';
import { LinearGradient } from '@vx/gradient';
import { ScaleSVG } from '@vx/responsive';

import {setgraph} from '../actions/index.js'


class Hourly extends React.Component {

    utcToDay(dt){
        let date = new Date((dt+this.props.maintimezone.timezoneoffset)*1000);
        return date.getUTCDay();
    }

    convert(unix_timestamp){
        let date = new Date((unix_timestamp+this.props.maintimezone.timezoneoffset)* 1000);
        let hours = date.getUTCHours();
        let minutes = "0" + date.getMinutes();
        let formattedTime = hours + (minutes.substr(-2))/60;

        return formattedTime;
    }

    rendergraph(){
        if (!this.props.hourlydata[0]) {return null;} //prevents calling while api is not loaded
        if (!this.props.currentcard.data && this.props.currentcard.data !== 0) {return null;} 
        if (!this.props.maindays[0]) {return null;} 

        let filtered = this.props.hourlydata.filter(data => (this.utcToDay(data.dt) === this.utcToDay(this.props.maindays[this.props.currentcard.data].dt)));
        
        if (this.props.currentcard.graph === "temp"){
            var data = (filtered.map(data =>  ({time: this.convert(data.dt), temp: data.main.temp})));
        }

        else if (this.props.currentcard.graph === "wind"){
            data = (filtered.map(data =>  ({time: this.convert(data.dt), temp: data.wind.speed})));
        }

        

        const width = 1000;
        const height = 400;
        
        const margin = {
            top: 60,
            bottom: 60,
            left: 80,
            right: 80,
        };
        const xMax = width - margin.left - margin.right;
        const yMax = height - margin.top - margin.bottom;
        
        const x = d => new Date(d.time); // d.time is unix timestamps
        const y = d => d.temp;

        const xScale = scaleLinear({
            range: [0, xMax],
            domain: extent(data, x)
        });
        const yScale = scaleLinear({
            range: [yMax, 0],
            domain: [min(data,y), max(data, y)],
        });

        const chart = (
            <ScaleSVG width={width} height={height}>
              <Group top={margin.top} left={margin.left}>

                <LinearGradient
                    from='#fbc2eb'
                    to='#a6c1ee'
                    id='gradient'
                />

                <AxisLeft
                    scale={yScale}
                    top={0}
                    left={0}
                    label={this.props.currentcard.graph === "temp" ? `Temp ${String.fromCharCode(176)}C` : 'Wind (Km)'}
                    stroke={'#1b1a1e'}
                    tickTextFill={'#1b1a1e'}
                />

                <AxisBottom
                    scale={xScale}
                    top={yMax}
                    label={'Hour of the Day'}
                    stroke={'#1b1a1e'}
                    tickTextFill={'#1b1a1e'}
                />


                <AreaClosed
                  data={data}
                  x={d => xScale(x(d))}
                  y={d => yScale(y(d))}
                  yScale={yScale}
                  fill={"url(#gradient)"}
                  stroke={"black"}
                />
              </Group>
            </ScaleSVG>
        )

        return chart;
    };

    
    render(){
        return (
            <React.Fragment>
                <div style= {{display: "flex", justifyContent: "center"}}>
                    <div style = {{width: "70%"}}>
                    {this.rendergraph()}
                    </div>
                </div>

                <div style= {{display: "flex", justifyContent: "center"}}>
                    <div style = {{width: "40%"}} className = "two ui buttons">
                        <button onClick = {() => this.props.setgraph("temp")} className={`ui button ${this.props.currentcard.graph === "temp" ? 'active' : ''}`}>
                            Temp
                        </button>

                        <button onClick = {() => this.props.setgraph("wind")} className={`ui button ${this.props.currentcard.graph === "wind" ? 'active' : ''}`}>
                            Wind
                        </button>
                        
                    </div>
                </div>

                <div style ={{height: "50px"}} />

                
            </React.Fragment>
        );
    };

};

const mapStateToProps = (state) => {
    return {
        hourlydata: Object.values(state.hourlyreducer),
        maindays: Object.values(state.mainreducer),
        maintimezone: state.timezonereducer,
        currentcard: state.currentcardreducer

    };
};

const hourlycomponent = connect(mapStateToProps, {setgraph})(Hourly);
export default hourlycomponent;