import React from 'react';
import {connect} from 'react-redux';

class Current extends React.Component {

    renderlocation(){
        if (!this.props.location.location) {return null;}

        if (!this.props.location.location.name) {return <h1>Unknown</h1>;}
        return <h1>{`${this.props.location.location.name}, ${this.props.location.location.country}`}</h1>;

    };

    convert(unix_timestamp){
        let date = new Date((unix_timestamp+this.props.timezone.timezoneoffset)* 1000);
        let days = ['Sunday','Monday','Tuesday','Wednesday','Thursday','Friday','Saturday'];
        let hours = date.getUTCHours();
        let minutes = "0" + date.getMinutes();
        let formattedTime = days[date.getUTCDay()] + ' ' + hours + ':' + minutes.substr(-2);
        return formattedTime;
    }

    renderdetails(){
        if (!this.props.current.current) {return null;}

        return (
            <React.Fragment>

                <div style= {{display: "flex", justifyContent: "center"}}>
                    <h3>{this.convert(this.props.current.current.dt)}</h3>
                </div>

                <div style= {{display: "flex", justifyContent: "center"}}>
                    <img src={`http://openweathermap.org/img/wn/${this.props.current.current.weather[0].icon}@2x.png`} alt = "weathericon" className = "ui image"/>
                </div>

                <div style= {{display: "flex", justifyContent: "center"}}>
                    <h3> 
                        {`${this.props.current.current.temp}${String.fromCharCode(176)}C  ||  Windchill: ${this.props.current.current.feels_like}${String.fromCharCode(176)}C`}
                    </h3>              
                </div>
            </React.Fragment>
        );
    }

    render(){
        return (
            <React.Fragment>
                
                <div style= {{display: "flex", justifyContent: "center"}}>
                    {this.renderlocation()}                   
                </div>

                {this.renderdetails()}                 

            </React.Fragment>
        );
    };

}

const mapStateToProps = (state) => {
    return {
        location: state.locationreducer,
        timezone: state.timezonereducer,
        current: state.currentdetailsreducer
    };
};

const currentcomponent = connect(mapStateToProps, null)(Current);
export default currentcomponent;