import React from 'react';
import {Field, reduxForm} from 'redux-form';
import {connect} from 'react-redux';

import {fetch5dayforecast, fetchhourly, fetchlocation, fetchtimezone, currentdetails, setcurrentcard} from '../actions/index.js'
import history from '../history'


class SearchForm extends React.Component {
    
    componentDidMount(){
        this.props.fetch5dayforecast(40.7128, -74.0060); //sets the intial location to new york
        this.props.fetchlocation(40.7128, -74.0060);
        this.props.fetchtimezone(40.7128, -74.0060);
        this.props.currentdetails(40.7128, -74.0060);
        this.props.fetchhourly(40.7128, -74.0060);
        if (!history.location.pathname[1]){ //redirects to proper url so it can select card and update state
            history.push('/0')
        }
        this.props.setcurrentcard(history.location.pathname[1]);
    }

    renderError(meta) {
        if (meta.touched && meta.error){
            return (
                <div className = "ui error message">
                    <div className = "header">{meta.error}</div>
                </div>
            );
        }

    };


    renderInput = (formProps) => {
        const className = `field ${formProps.meta.error && formProps.meta.touched ? 'error' : ''}`;
        return (
            <div className = {className}>
                <label>{formProps.label}</label>
                <input {...formProps.input} autoComplete = "off"/>
                {this.renderError(formProps.meta)}
            </div>
        );
    };

    onSubmit = (formValues) => {
        this.props.fetchhourly(formValues.latitude, formValues.longitude);
        this.props.fetch5dayforecast(formValues.latitude, formValues.longitude);
        this.props.fetchlocation(formValues.latitude, formValues.longitude);
        this.props.fetchtimezone(formValues.latitude, formValues.longitude);
        this.props.currentdetails(formValues.latitude, formValues.longitude);

    };

    render(){
        return (
            <div style= {{display: "flex", justifyContent: "center"}}>
            <form onSubmit = {this.props.handleSubmit(this.onSubmit)} className="ui form error" style= {{width: "60%" }}>
                <div className = "two fields">
                    
                <Field name = "latitude" component = {this.renderInput} label = "Enter Latitude"/>
                <Field name = "longitude" component = {this.renderInput} label = "Enter Longitude"/>
                </div>
                <button className = "ui button secondary fluid">Submit</button>
            </form>
            </div>
        );
    };

};

const validate = (formValues) =>{
    const errors = {};

    if ( formValues.longitude > 180 ){
        errors.longitude = "Invalid Longitude"
    }

    if ( formValues.latitude > 90 ){
        errors.latitude = "Invalid Latitude"
    }

    if ( formValues.longitude < -180 ){
        errors.longitude = "Invalid Longitude"
    }

    if ( formValues.latitude < -90 ){
        errors.latitude = "Invalid Latitude"
    }

    if (formValues.longitude && isNaN(formValues.longitude) ){
        errors.longitude = "Invalid Longitude"
    }

    if (formValues.latitude && isNaN(formValues.latitude)){
        errors.latitude = "Invalid Latitude"
    }
    
    return errors;
};

const Search =  reduxForm({form: 'streamForm', validate})(SearchForm);
const searchcomponent = connect(null, {fetchlocation, fetch5dayforecast, fetchhourly, fetchtimezone, currentdetails, setcurrentcard})(Search);

export default searchcomponent;