import React from 'react';
import {connect} from 'react-redux';
import {Link} from 'react-router-dom';

import {fetch5dayforecast, fetchtimezone, setcurrentcard} from '../actions/index.js'
import history from '../history'

class Card extends React.Component {

    renderdate(dt){
        let date = (new Date((dt+this.props.timezone.timezoneoffset)*1000)).toUTCString();
        return String(date).substr(4,13); //remove the time off the dates
    }

    renderweekday(dt){
        let date = new Date((dt+this.props.timezone.timezoneoffset)*1000);
        let days = ['Sunday','Monday','Tuesday','Wednesday','Thursday','Friday','Saturday'];
        return days[date.getUTCDay()];
    }

    renderimage(icon){
        return <img src={`http://openweathermap.org/img/wn/${icon}@2x.png`} alt = "weathericon" className = "ui center aligned tiny image"/>;
    }

    renderfeelslike(temp){
        return(
            <React.Fragment>
                <div className="center aligned header">
                    Windchill
                </div>

                <div className="center aligned description">
                    Morn: {temp.morn}{String.fromCharCode(176)}C
                    <br/>
                    Day: {temp.day}{String.fromCharCode(176)}C
                    <br/>
                    Eve: {temp.eve}{String.fromCharCode(176)}C
                    <br/>
                    Night: {temp.night}{String.fromCharCode(176)}C
                </div>
            </React.Fragment>
        );
    }

    rendersun(sunrise, sunset){
        let convert = (unix_timestamp) => {
            let date = new Date((unix_timestamp+this.props.timezone.timezoneoffset)* 1000);
            let hours = date.getUTCHours();
            let minutes = "0" + date.getMinutes();
            let seconds = "0" + date.getSeconds();
            let formattedTime = hours + ':' + minutes.substr(-2) + ':' + seconds.substr(-2);
            return formattedTime;
        }

        return (
            <React.Fragment>
            
                <div className="center aligned ui sub header">
                    Sunrise: 
                    <br/>
                    {convert(sunrise)}
                    <br/>
                    <br/>
                    Sunset: 
                    <br/>
                    {convert(sunset)}
                </div>

            </React.Fragment>
        )
    }

    rendercards(){ //will create all 5 cards
        if (!this.props.weatherdata[0]) {return null;} //prevents calling while api is not loaded
        if (!this.props.timezone) {return null;}

        return this.props.weatherdata.map((data,index) => {
            if (index >4){return null;} //only render 5 cards

            return(
            <Link to= {`/${index}`} onClick = {() => this.props.setcurrentcard(index)} key = {index} className={`${"/" + index === history.location.pathname ? 'black' : ''} card`} >

                <div className="ui card slide masked reveal" style={{flex: "1"}}> 
                <div className = "visible content">

                    <div className = "center aligned header">
                        {this.renderweekday(data.dt)}
                    </div>

                    <div className="center aligned description">
                        {this.renderdate(data.dt)}
                    </div>

                    <br/>
                    <div className="center aligned">
                        {this.renderimage(data.weather[0].icon)}
                    </div>

                    <h1 className = "center aligned ui sub header">
                        {`High: ${data.temp.max}${String.fromCharCode(176)}C`}
                        <br />
                        <br />
                        {`Low: ${data.temp.min}${String.fromCharCode(176)}C`}
                    </h1>    

                    <div className="center aligned description">
                        {data.weather[0].description}
                    </div>
                </div>

                <div className = "hidden content">
                    {this.renderfeelslike(data.feels_like)}
                    
                    {this.rendersun(data.sunrise, data.sunset)}
                </div>

                </div>
            </Link>
            );
        });
        
    };

    render(){
        return (
            <div style= {{display: "flex", justifyContent: "center"}}>
                <div style= {{width: "70%" }}>
                    <div className = "ui five doubling cards centered">
                        {this.rendercards()}
                    </div>
                </div>
            </div>
        );
    };

};

const mapStateToProps = (state) => {
    return {
        weatherdata: Object.values(state.mainreducer),
        timezone: state.timezonereducer,
    };
};

const cardcomponent = connect(mapStateToProps, {fetch5dayforecast, fetchtimezone, setcurrentcard})(Card);
export default cardcomponent;