import {combineReducers} from 'redux';
import {reducer} from 'redux-form';

import {mainreducer, hourlyreducer, locationreducer, timezonereducer, currentcardreducer, currentdetailsreducer} from './mainreducer.js';


export default combineReducers({
    mainreducer: mainreducer,
    hourlyreducer: hourlyreducer,
    form: reducer,
    locationreducer: locationreducer,
    timezonereducer: timezonereducer,
    currentcardreducer: currentcardreducer,
    currentdetailsreducer: currentdetailsreducer
});