import _ from 'lodash'

export const mainreducer = (state = {}, action) =>{
    switch(action.type){
        case "LIST_FORECAST":
            return {..._.mapKeys(action.payload, 'dt')};

        default:
            return state;
    };
};

export const hourlyreducer = (state = {}, action) =>{
    switch(action.type){
        case "LIST_HOURLY":
            return {..._.mapKeys(action.payload, 'dt')};

        default:
            return state;
    };
};

export const locationreducer = (state = {}, action) =>{
    switch(action.type){
        case "FETCH_LOCATION":
            return {...state, "location": action.payload};

        default:
            return state;
    };
};

export const timezonereducer = (state = {}, action) =>{
    switch(action.type){
        case "FETCH_TIMEZONE":
            return {...state, "timezoneoffset": action.payload};

        default:
            return state;
    };
};

export const currentcardreducer = (state={"graph": "temp"}, action) => {
    switch(action.type){
        case "SET_CURRENTCARD":
            return {...state, "data": action.payload}; 

        case "SET_GRAPH":
            return {...state, "graph": action.payload};

        default:
            return state;
    };

}

export const currentdetailsreducer = (state={}, action) => {
    switch(action.type){
        case "FETCH_CURRENTDETAILS":
            return {"current": action.payload};  
        
        default:
            return state;
    };

}